class ShowApi {
  static getAllShows(page) {
    // console.log('Page ' + page)
    return fetch('https://api.themoviedb.org/3/tv/popular?api_key=ae4678f4b0f74b45a9df7c17fc3fb6c5&language=en-US&page=' + page).then(response => {
      return response.json();
    }).catch(error => {
      return error;
    });
  }
  static getSearchShows(page,title) {
    // console.log('Page ' + page)
    return fetch('https://api.themoviedb.org/3/search/tv?api_key=ae4678f4b0f74b45a9df7c17fc3fb6c5&query=' + title+'&page=' + page).then(response => {
      return response.json();
    }).catch(error => {
      return error;
    });
  }
}
export default ShowApi; 


