import ShowApi from '../api/show-api';

export function loadShows(page) {
    return function (dispatch) {
        return ShowApi.getAllShows(page).then(shows => {
            // console.log(shows);
            dispatch(loadShowsSuccess(shows));
        }).catch(error => {
            throw (error);
        });
    };
}

export function loadShowsSuccess(shows) {  
    // console.log(shows);
    return {type: 'LOAD_SUCCESS', shows};
}

export function loadSearchShows(page,title) {
    // console.log(title);
    return function (dispatch) {
        return ShowApi.getSearchShows(page,title).then(shows => {
            // console.log(shows);
            dispatch(loadShowsSuccess(shows));
        }).catch(error => {
            throw (error);
        });
    };
}


