export default function (state = null, action) {
    switch (action.type) {
        case 'LOAD_SUCCESS':
            // console.log(action.shows);
            return action.shows;
        default:
            return state;
    }
}

export function pagesCount(state = null, action) {
    switch (action.type) {
        case 'LOAD_SUCCESS':
            // console.log(action.shows);
            return action.shows.total_pages;
        default:
            return state;
    }
}


