import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { selectShow } from '../actions';
import ShowDetail from './show-detail';
import './showcard.css';
import { loadShows, loadSearchShows } from '../actions/load-show';
import Pagination from "react-js-pagination";
import { asyncContainer, Typeahead } from 'react-bootstrap-typeahead';
import Loader from '../Loader';
import '../Loader.css';

const AsyncTypeahead = asyncContainer(Typeahead);
class ShowList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activePage: 1,
      pageCount: 1000,
      pageRange: 10,
      activeSearchPage: 1,
      pageCountSearch: 0,
      pageRangeSearch: 5,
      isLoading: true,
      searchMode: false,
      shows: null
    };
    this.handlePageChange = this.handlePageChange.bind(this);
    this.handleSearchPageChange = this.handleSearchPageChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.setPageCount = this.setPageCount.bind(this);
    this.createListItems = this.createListItems.bind(this);
  }

  handlePageChange(pageNumber) {
    console.log(`Viewing Page: ${pageNumber}`);
    this.setState({ activePage: pageNumber });
    this.props.selectPage(pageNumber);
  }
  handleSearchPageChange(pageNumber) {
    this.setState({ activeSearchPage: pageNumber, pageCount:this.props.pageC });
    this.props.searchShow(pageNumber,this.state.searchQuery);
  }
  setPageCount() {
    this.setState({
      pageCount: this.props.shows['total_pages'] > 1000 ? 1000 : this.props.shows['total_pages']
      // Max 1000
    });
  }
  createListItems() {
    if (this.state.pageCount === 0) {
      // this.setPageCount();
      // console.log(this.props.shows["total_pages"]);
      // console.log(this.props.pageC);
    }
    return this.props.shows['results'].map((show) => {
      return (
        <div className="col-md-3 text-center" key={show.id}>
          <a align='center' onClick={() => this.props.selectShow(show)} data-toggle="modal" data-target="#exampleModalCenter">
            <img className='showcardbox' src={('https://image.tmdb.org/t/p/w500' + show.poster_path)} alt={show.name} style={{ cursor: 'pointer', width: '200px', height: 'auto', margin: '20px 0px' }} />
            <br />
            <p className="title" style={{ cursor: 'pointer' }}>{show.name}</p>
          </a>
          <div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  {/* <h5 className="modal-title" id="exampleModalCenterTitle"></h5> */}
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  <ShowDetail />
                </div>
                <div className="modal-footer">
                  <button type="button" className="btn btn-danger" data-dismiss="modal">Close</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    });
  }
  handleSearch(event) {
    if (event[0] != null) {
      // console.log(event[0]);
      this.setState({ searchMode: true, searchQuery: event[0].name, pageCount: 0 })
      this.props.searchShow(this.state.activeSearchPage,event[0].name);
    }
  }
  render() {
    return (
      <div className='container'>
        <AsyncTypeahead
          labelKey={(option) => `${option.name}`}
          isLoading={this.state.isLoading}
          placeholder="Search Tv Shows"
          onChange={this.handleSearch}
          onSearch={query => {
            // console.log(query);
            this.setState({ isLoading: true, pageCount:0 });
            fetch(`https://api.themoviedb.org/3/search/tv?api_key=ae4678f4b0f74b45a9df7c17fc3fb6c5&query=${query}`)
              .then(resp => resp.json())
              .then(json => {
                this.setState({
                  isLoading: false,
                  options: json.results
                })
                // console.log(json.results);
              });
          }}
          options={this.state.options}
        />
        <br />
        {this.state.searchMode &&
          <div>
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb">
                <li className="breadcrumb-item"><a href="" onClick={() => { this.setState({ activeSearchPage: 1,activePage: 1, searchMode: false }); this.handlePageChange(1) }}>Home</a></li>
                <li className="breadcrumb-item active" aria-current="page">Search</li>
              </ol>
            </nav>

            <Pagination
              innerClass="pagination justify-content-center flex-wrap"
              itemClass="page-item"
              linkClass="page-link"
              activePage={this.state.activeSearchPage}
              itemsCountPerPage={1}
              totalItemsCount={this.props.pageC}
              pageRangeDisplayed= {this.props.pageC>5?5:this.props.pageC}
              onChange={this.handleSearchPageChange}
            />
            <div className='row'>
              {this.props.shows && this.createListItems()}
              <div className='col'>
                {!this.props.shows && <Loader />}
              </div>
            </div>
          </div>}
        {!this.state.searchMode && <div>

          <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item active"><a href="" onClick={() => { this.setState({ activeSearchPage: 1,activePage: 1, searchMode: false }); this.handlePageChange(1) }}>Home</a></li>
            </ol>
          </nav>
          <Pagination
            innerClass="pagination justify-content-center flex-wrap"
            itemClass="page-item"
            linkClass="page-link"
            activePage={this.state.activePage}
            itemsCountPerPage={1}
            totalItemsCount={this.state.pageCount}
            pageRangeDisplayed={this.state.pageRange}
            onChange={this.handlePageChange}
          />
          <div className='row'>
            {this.props.shows && this.createListItems()}
            <div className='col'>
              {!this.props.shows && <Loader />}
            </div>
          </div>
          <p style={{ backgroundColor: 'wheat', display: 'inline-block', padding: '10px' }}>Shows : {this.state.activePage * 20 - 19} - {this.state.activePage * 20} || Page : {this.state.activePage} of {this.state.pageCount}</p>
          <Pagination
            innerClass="pagination justify-content-center flex-wrap"
            itemClass="page-item"
            linkClass="page-link"
            activePage={this.state.activePage}
            itemsCountPerPage={1}
            totalItemsCount={this.state.pageCount}
            pageRangeDisplayed={this.state.pageRange}
            onChange={this.handlePageChange}
          />
        </div>}

      </div>
    );
  }
}

function mapStateToProps(state) {
  // console.log(state.shows);
  // console.log(state.pageC);
  return {
    show: state.activeShow,
    shows: state.shows,
    pageC: state.pageC
  };
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators({ selectShow: selectShow, selectPage: loadShows, searchShow: loadSearchShows }, dispatch)
}
export default connect(mapStateToProps, matchDispatchToProps)(ShowList);


