import React, { Component } from 'react';
import {connect} from 'react-redux';

class ShowDetail extends Component {
    render() {
        if(!this.props.show){
            return(<h4>Select a show....</h4>);
        }
        return (
            <div>
                <h2>{this.props.show.name}</h2>
                <p>{this.props.show.overview}</p>
                <hr/>
                <p><strong>Air Date: </strong>{this.props.show.first_air_date}</p>
                <p><strong>Average Rating: </strong>{this.props.show.vote_average} / 10</p>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        show: state.activeShow
    };
}

export default connect(mapStateToProps)(ShowDetail);


